import React from 'react';

const Detail = ({ location: {state}, history }) => {
     // console.log(state);
     if(state === undefined) {
          history.push('/');
          return null;
     } else {
          return (
               <div>
                    <div>{state.title}</div>
                    <div>{state.summary}</div>
               </div>
          )
     }
}

